<template>
  <div class="container">
    <div class="new-post">
      <input type="text" class="post-input" placeholder="What's on your mind?" v-model="text">
      <button v-on:click="createPost()">Save</button>
    </div>
    <h1>Latest Posts</h1>
    <p class="error" v-if="error">{{error}}</p>
      <div class="posts-container">
      <ul>
        <li v-for="(post,index) in posts" v-bind:item ="post" v-bind:index="index" v-bind:key="post._id">
          <button class="delete" v-on:click="deletePost(post._id)">x</button>
          <p>{{`${post.created_at.getFullYear()}/${post.created_at.getMonth()}/${post.created_at.getDate()}`}}</p>
          <p>{{post.text}}</p>
          </li>
      </ul>
      </div> 
  </div>
</template>

<script>
import PostService from '../PostService'

export default {
  name: 'PostsComponent',
  props: {
    msg: String
  },
  data(){
    return {
      posts: [],
      text: '',
      error: '' 
    }
  },
  async created(){
    try {this.posts = await PostService.getPosts()}
    catch(err){
      this.error = err.message
    }
  },
  methods: {
    async createPost(){
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts()
    },
    async deletePost(id){
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts()
    }
  }
}
</script>

